.App {
  text-align: center;
}

.flexElem{
  display: flex;
}
.flexColumn {
  display: flex;
  flex-direction: column;
}
.flexCenter{
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexGrow {
  flex-grow: 1;
}
.fullHeight {
  height: 100%;
}
.fullWidth {
  width: 100%;
}

.paddingHoriz{
  padding: 0 1em;
}
.paddingTop{
  padding-top: 1em;
}
.paddingLeft05{
  padding-left: 0.5em;
}
.padding1em3em{
  padding: 1em 3em;
}

.leftAlign{
  text-align: left;
}

.boldText{
  font-weight: bold;
}
.smallText {
  font-size: 80%;
}

.upend {
  transform: rotate(180deg);
}

.colorBlack {
  color: black;
}

.disablSort {
  color: lightgray;
}
.disablSort:hover {
  color: grey;
}

.circularProgress {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.iconButton:hover {
  background-color: lavender !important;
}

/* --- Layout --- */
.headerPan,
.mapPan,
.listPan,
.footerPan
{
  position: absolute;
  min-width: 260px;
}

.headerPan {
  display: flex;
  align-items: center;
  top: 0em;
  left: 0em;
  right: 0em;
  height: 2em;
  background-color: SkyBlue;
}
.mapPan {
  background-color:MediumAquamarine;
}
.footerPan {
  display: flex;
  align-items: center;
  bottom:		0em;
  left:		0em;
  right:		0em;
  height:		1.2em;
  padding-left: 0.5em;
  white-space: pre;
  overflow: hidden;
  background-color: lightgray;
}

/* LANDSCAPE */
@media screen and (orientation:landscape){
	.mapPan {
		top: 2em;
		bottom: 1.2em;
		left: 0em;
		right: 25em;
	}
	.listPan{
		top: 2em;
		bottom: 1.2em;
		right: 0em;
    width: 25em;
	}
}

/* PORTRAIT */
@media screen and (orientation:portrait){
	.mapPan {
		top: 2em;
		bottom: 21.2em;
		left: 0em;
		right: 0em;
	}
	.listPan{
		bottom: 1.2em;
		left: 0em;
		right: 0em;
    height: 20em;
	}
}


/* --- header --- */
.headerIcon {
  width: 1.6em;
  height: 1.6em;
}
.headerName {
  display: flex;
  align-items: center;
  cursor: default;
}
.headerBlock {
  display: flex;
  align-items: center;
  cursor: pointer;
  border-radius: 12px;
}
.headerBlock:hover {
  background-color: LightBlue;
}

/* --- map --- */
#map {
  position: absolute;
  top: 0%; bottom:0%;
  left:0%; right: 0%;
}


/* --- Elements --- */

.elementsHeader {
  display: flex;
  width: 100%;
  height: 38px;
  padding: 1px;
}
.elementsBody {
  display: flex;
  width: 100%;
  height: 100%;
}

.buttonIcon {
  width: 1em;
  height: 1em;
}
.width25 {
  width: 25em;
}
.height21 {
  height: 21em;
}

.sortHeader {
  display: flex;
  align-items: center;
}

.expText {
  font-size: 80%;
  white-space: pre-wrap;
}

.scrollList{
  max-height: 25em;
  overflow-y: auto;
}

/* --- rvTable --- */

.listContainer {
  width: 100%;
  flex-grow: 1;
  background: white;
}
.listHeader {
  width: 100%;
  height: 30px;
  background-color: lightgray;
}
.listCell {
  border-bottom: solid 1px grey;
  overflow-y: hidden;
}
.listExpCell {
  border-bottom: solid 1px grey;
  overflow-y: auto;
}
.cellContent {
  width:100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.listRowSelected {
  background: SkyBlue;
}
.textCell {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: default;
  padding: 0 4px;
}
